.mt-neg-200 {
    margin-top: -200px;
    position: relative;
    z-index: 1;
}


#wrapper-navbar + .wrapper {
    padding: 0;
}

#main > article > .entry-content > .wp-block-cover:first-child,
#search-wrapper,
#error-404-wrapper,
#index-wrapper {
    padding-top: 10rem !important;
}

#search-wrapper,
#error-404-wrapper {
    
    .page-header {
        margin-bottom: $spacer * 2;
    }

}

.stretch-linked-block {
    position: relative !important;
    transition: $transition-fade;

    .editorskit-block-link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 90;
    }

    &:hover {
        opacity: .7;

        .wp-block-button.is-style-arrow-link .wp-block-button__link:after {
            right: 0;
        }
    }
}

.stretched-link {
    color: $body-color;

    &:hover {
        text-decoration: none;
        color: $body-color;
    }
}

.entry-footer {
    position: relative;
    z-index: 2;
}

.eocjs-newsticker {
    text-transform: uppercase;
    background-color: $black !important;
    color: $white;
}